import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Link from 'next/link'
import ItemStyles from '../components/styles/ItemStyles'
import Title from '../components/styles/Title'
import PriceTag from '../components/styles/PriceTag'
import styled from 'styled-components';


const GeneralInfoStyled = styled.div `
display:grid;
grid-template-columns:1fr;
grid-auto-flow:column;
.itemInfo{
text-align:left;
border-bottom: 1px solid lightgrey;
width:50%; 

}
`;

class Candidate extends Component {
    static propTypes = {
        eachCandidate: PropTypes.object.isRequired
    }

    render() {
        const {
            image,
            id,
            candCode,
            cand1stName,
            cand2ndName,
            cand3rdName,
            email,
            phoneNumb,
            placeOfBirth
        } = this.props.eachCandidate
        return (

                <ItemStyles>
                    {image && <img  src={image} alt={cand1stName}/>}
                    <Title>
                        <Link
                            href={{
                            pathname: "/candidatePage",
                            query: {
                                id: id
                            }
                        }}>
                            <a>
                                {cand1stName}
                            </a>
                        </Link>
                    </Title>
   <div className="restInfo">
                        <div className="itemInfo"><p>Phone:{phoneNumb}</p></div>
                        <div className="itemInfo"><p>Name:{cand1stName}</p></div>
                        <div className="itemInfo"><p>2nd Name:{cand2ndName}</p></div>
                        <div className="itemInfo"><p>last Name{cand3rdName}</p></div>
                        <div className="itemInfo"><p>Code:{candCode}</p></div>
                        <div className="itemInfo"><p>Email:{email}</p></div>
                        <div className="itemInfo"><p>Birth Place:{placeOfBirth}</p></div>
                        </div>
                    <div className="buttonList">
                        <Link
                            href={{
                            pathname: "/update",
                            query: {
                                id: id
                            }
                        }}>
                            <a>
                                Edit
                            </a>
                        </Link>
                        <button>Delete</button>
                    </div>
                </ItemStyles>
        )
    }
}
export default Candidate;